<template>
	<v-container fluid>
		<v-row align>
			<v-col cols="12" md="8">
				<div class="title">
					NOTE:
				</div>
				<div class="body-1">
					Please select a value between
					<template v-if="method == 4">
						<span class="font-weight-bold"
							>${{
								numberWithCommas(
									Number(this.data.fsa_current_annual_election) + 10,
								)
							}}</span
						>
					</template>
					<template v-else>
						<span class="font-weight-bold"
							>${{
								numberWithCommas(
									Math.max(
										Number(this.data.ytd_Total_Claim),
										Number(this.data.fsa_ytd_deduction),
									),
								)
							}}</span
						>
					</template>
					and
					<template v-if="method == 4">
						<span class="font-weight-bold"
							>${{
								numberWithCommas(Number(data.fsa_max_electable_increase))
							}}</span
						>
					</template>
					<template v-else>
						<span class="font-weight-bold"
							>${{
								numberWithCommas(Number(data.fsa_current_annual_election) - 10)
							}}</span
						>
					</template>
					<v-menu right offset-x>
						<template v-slot:activator="{ on }">
							<v-btn icon v-on="on">
								<v-icon small>far fa-info-circle</v-icon>
							</v-btn>
						</template>

						<v-card max-width="350">
							<v-card-text class="body-1">
								This range is based on an industry standard formula
								incorporating your current annual election, claims reimbursed,
								pending claim reimbursements and your employer's minimum and
								maximum annual election amounts.
							</v-card-text>
						</v-card>
					</v-menu>
				</div>
			</v-col>
		</v-row>
		<v-row class="pa-0">
			<v-col cols="12" md="8">
				<v-text-field
					label="Enter your revised FSA annual election"
					outlined
					prefix="$"
					@keydown.enter="stepT"
					:rules="[rulesMeth]"
					type="number"
					v-model="newFSAval"
				></v-text-field>
			</v-col>
		</v-row>
	</v-container>
</template>
<script>
	export default {
		name: 'theUpdate',
		methods: {
			stepT() {
				if (
					Number(this.newFSAval) >= Number(this.data.ytd_Total_Claim) &&
					Number(this.newFSAval) <= Number(this.data.fsa_max_electable_increase)
				) {
					this.$emit('entered');
				}
			},
			rulesMeth: function() {
				if (this.method == 3) {
					if (
						Number(this.newFSAval) <
						Math.max(
							Number(this.data.ytd_Total_Claim),
							Number(this.data.fsa_ytd_deduction),
						)
					) {
						return (
							'You must elect at least $' +
							this.numberWithCommas(
								Math.max(
									Number(this.data.ytd_Total_Claim),
									Number(this.data.fsa_ytd_deduction),
								),
							)
						);
					} else if (
						Number(this.newFSAval) >
						Number(this.data.fsa_current_annual_election) - 10
					) {
						return (
							'You cannot exceed $' +
							this.numberWithCommas(
								Number(this.data.fsa_current_annual_election) - 10,
							)
						);
					}
				} else if (this.method == 4) {
					if (
						Number(this.newFSAval) <
						Number(this.data.fsa_current_annual_election)
					) {
						return (
							'You must elect at least $' +
							this.numberWithCommas(
								Number(this.data.fsa_current_annual_election),
							)
						);
					} else if (
						Number(this.newFSAval) >
						Number(this.data.fsa_max_electable_increase)
					) {
						return (
							'You cannot exceed $' +
							this.numberWithCommas(this.data.fsa_max_electable_increase)
						);
					}
				}
			},
			numberWithCommas: function(x) {
				return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
			},
		},
		watch: {
			newFSAval: function() {
				this.$emit('enteredval', this.newFSAval);
			},
		},

		data: () => ({
			newFSAval: null,
			maxVal: null,
		}),

		props: ['data', 'method'],
	};
</script>
