<template>
	<v-app>
		<div>
			<v-app-bar color="transparent" flat dense :dark="goDark">
				<v-spacer></v-spacer>

				<v-spacer></v-spacer>

				<v-menu left bottom>
					<template v-slot:activator="{ on }">
						<v-btn icon v-on="on">
							<v-icon>mdi-dots-vertical</v-icon>
						</v-btn>
					</template>

					<v-list>
						<v-list-item @click="goDark = false">
							<v-list-item-title>Light</v-list-item-title>
						</v-list-item>
						<v-list-item @click="goDark = true">
							<v-list-item-title>Dark</v-list-item-title>
						</v-list-item>
						<v-divider></v-divider>
						<v-list-item @click="currentFSASel = 0">
							<v-list-item-title>Not Participating in FSA</v-list-item-title>
						</v-list-item>
						<v-list-item @click="currentFSASel = 1">
							<v-list-item-title>Participating in FSA</v-list-item-title>
						</v-list-item>
					</v-list>
				</v-menu>
			</v-app-bar>
		</div>
		<v-content>
			<v-container fluid>
				<v-row align="center" justify="center">
					<v-col cols="12" sm="8">
						<div class="display-1 font-weight-light mb-3">FSA Modification</div>
					</v-col>
				</v-row>
				<v-row align="center" justify="center">
					<v-col cols="12" sm="8">
						<v-card :loading="submitting">
							<v-card-title
								class="title font-weight-regular justify-space-between"
							>
								<div>
									<div>{{ currentTitle }}</div>
									<v-chip :color="currentModChip" dark v-if="step != 4">
										<v-fade-transition>
											<div v-if="step < 4" class="subtitle-1">
												{{ currentMod }}
											</div>
										</v-fade-transition>
									</v-chip>
								</div>
								<v-avatar
									color="primary lighten-2"
									class="subheading white--text"
									size="24"
									v-text="step"
								></v-avatar>
							</v-card-title>
							<v-window v-model="step" touchless>
								<v-window-item :value="1">
									<v-card-text>
										<theSelection
											:currentFSA="currentFSASel"
											@selected="selectModMethod"
											@selectedfsa="selectMod"
											:currentHSA="true"
										></theSelection>
									</v-card-text>
								</v-window-item>
								<v-window-item :value="2">
									<v-card-text>
										<template v-if="selectedFsa == 2">
											<theDisclaimer></theDisclaimer>
										</template>
										<template v-else>
											<theUpdate
												@enteredval="changeHandler"
												@entered="advanceStep"
												:data="eeData"
												:method="selectedFsa"
											></theUpdate>
										</template>
									</v-card-text>
								</v-window-item>

								<v-window-item :value="3">
									<div class="pa-4 pb-md-12 text-center">
										<v-progress-circular
											size="70"
											color="primary"
											indeterminate
										></v-progress-circular>
									</div>
								</v-window-item>
								<v-window-item :value="4">
									<div class="pa-4 text-center">
										<div>
											<i class="fad fa-check fa-5x success--text"></i>
										</div>
										<h3 class="title font-weight-light mb-2">
											Changes succesfully submitted!
										</h3>
									</div>
								</v-window-item>
							</v-window>
							<v-divider></v-divider>
							<template v-if="step < 3">
								<v-card-actions>
									<v-btn v-if="step > 1 && step < 4" text @click="step--"
										>Back</v-btn
									>
									<v-spacer></v-spacer>
									<v-btn
										:disabled="
											step > 3 ||
												selectedFsa == null ||
												inputValid ||
												step3Invalid
										"
										v-if="step < 4"
										color="primary"
										depressed
										@click="advanceStep"
										>Next</v-btn
									>
								</v-card-actions>
							</template>
						</v-card>
					</v-col>
				</v-row>
				<v-row justify="center">
					<v-dialog v-model="confirmDialog" persistent max-width="500">
						<v-card>
							<v-card-title class="headline">Submit FSA Changes</v-card-title>
							<v-card-text>
								<v-container fluid>
									<v-row>
										<v-col class="pa-0">
											<div class="body-1">
												By clicking &rdquo;I Agree&ldquo;, you agree to the
												following changes
											</div>
										</v-col>
									</v-row>
									<v-row>
										<v-col class="pa-0 mt-8">
											<v-card outlined>
												<v-card-text>
													<v-simple-table>
														<template v-slot:default>
															<tbody>
																<tr>
																	<td class="font-weight-bold title">
																		Modification
																	</td>
																	<td>{{ currentMod }}</td>
																</tr>
																<template v-if="selectedFsa != 2">
																	<tr>
																		<td class="font-weight-bold title">
																			Curr. Annual Elect.
																		</td>
																		<td>
																			${{
																				numberWithCommas(
																					eeData.fsa_current_annual_election,
																				)
																			}}
																		</td>
																	</tr>
																</template>

																<template v-if="selectedFsa != 2">
																	<tr>
																		<td class="font-weight-bold title">
																			New Annual Elect.
																		</td>
																		<td>${{ numberWithCommas(newFSA) }}</td>
																	</tr>
																</template>
															</tbody>
														</template>
													</v-simple-table>
												</v-card-text>
											</v-card>
										</v-col>
									</v-row>
								</v-container>
							</v-card-text>
							<v-card-actions>
								<v-spacer></v-spacer>
								<v-btn
									color="green darken-1"
									text
									@click="confirmDialog = false"
									>I Disagree</v-btn
								>
								<v-btn color="green darken-1" text @click="acceptDialog"
									>I Agree</v-btn
								>
							</v-card-actions>
						</v-card>
					</v-dialog>
				</v-row>
			</v-container>
		</v-content>
	</v-app>
</template>

<script>
	import theSelection from '@/components/theSelection.vue';
	import results from '@/store/sample.js';
	import theUpdate from '@/components/update.vue';
	import theDisclaimer from '@/components/theDisclaimer.vue';
	export default {
		name: 'fsa',
		components: {
			theSelection,
			theUpdate,
			theDisclaimer,
		},
		data: () => ({
			step: 1,
			currentMod: '',
			selectedFsa: '',
			goDark: false,
			submitting: false,
			currentFSASel: 1,
			confirmDialog: false,
			eeData: results.sample[0],
			newFSA: '',
		}),
		methods: {
			changeHandler(value) {
				this.newFSA = value;
			},
			acceptDialog() {
				this.confirmDialog = false;
				this.step++;
			},
			advanceStep() {
				if (this.step == 2) {
					this.confirmDialog = true;
				} else {
					this.step++;
				}
			},
			selectModMethod(value) {
				this.currentMod = value;
			},
			selectMod(value) {
				this.selectedFsa = value;
			},
			numberWithCommas: function(x) {
				return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
			},
		},
		watch: {
			step: function() {
				var self = this;
				if (self.step == 3) {
					self.submitting = true;
					setTimeout(function() {
						self.step = 4;
					}, 3000);
				} else {
					this.submitting = false;
				}
			},
			goDark: function() {
				if (this.goDark) {
					this.$vuetify.theme.dark = true;
				} else {
					this.$vuetify.theme.dark = false;
				}
			},
		},
		computed: {
			step3Invalid() {
				return this.newFSA == '' && this.step == 2 && this.selectedFsa != 2;
			},
			inputValid() {
				return (
					(Number(this.newFSA) >
						Number(this.eeData.fsa_max_electable_increase) &&
						this.selectedFsa != 2 &&
						this.step == 2) ||
					(Number(this.newFSA) < Number(this.eeData.ytd_Total_Claim) &&
						this.step == 2 &&
						this.selectedFsa != 2)
				);
			},
			currentModChip() {
				switch (this.selectedFsa) {
					case '1':
						return 'green accent-4';
					case '2':
						return 'red accent-4';
					default:
						return 'primary';
				}
			},
			currentTitle() {
				switch (this.step) {
					case 1:
						return 'Type of Modification';
					case 2:
						return 'Make your modification';
					case 3:
						return 'Submitting';
					default:
						return 'Confirmation';
				}
			},
		},
	};
</script>
